import uniStatusBar from "../../../component/index/uni-status-bar.vue"
import sMineThirdLogin from "../../../component/mine/s-mine-third-login.vue"
export default {
	components: {
		uniStatusBar,
		sMineThirdLogin
	},
	data() {
		return {
			statuesColor: "#ffe933",
			accountLogin: false,
			codeTime: 0,
			btnDisable: true,
			btnClick: false,
			phoneNum: "",
			authCode: "",
			accountNum: "",
			pwdNum: ""
		}
	},
	methods: {
		handleClose() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSwitchLoginType() {
			this.accountLogin = !this.accountLogin
			this.phoneNum = ""
			this.authCode = ""
			this.accountNum = ""
			this.pwdNum = ""
		},
		handleGetMegCode() {
			if (!this.isValidPhone()) {
				uni.showToast({
					title: "请输入合法的手机号",
					icon: "none"
				})
				return;
			}
			if (this.codeTime > 0) {
				return;
			}
			this.codeTime = 10
			var timer = setInterval(() => {
				this.codeTime--
				if (this.codeTime < 1) {
					clearInterval(timer)
				}
			}, 1000)
		},
		handleLogin() {
			console.log("dsfsdf")
		},
		isValidPhone() {
			if (!(/^1[34578]\d{9}$/.test(this.phoneNum))) {
				return false
			} else {
				return true
			}
		}
	},
	watch: {
		phoneNum(val) {
			if (this.phoneNum && this.phoneNum != null && this.authCode && this.authCode != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		authCode(val) {
			if (this.phoneNum && this.phoneNum != null && this.authCode && this.authCode != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		accountNum(val) {
			if (this.accountNum && this.accountNum != null && this.pwdNum && this.pwdNum != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		},
		pwdNum(val) {
			if (this.accountNum && this.accountNum != null && this.pwdNum && this.pwdNum != null) {
				this.btnDisable = false
				this.btnClick = true
			} else {
				this.btnDisable = true
				this.btnClick = false
			}
		}
	}
}
